
import './App.css';
import {useEffect} from 'react';

function App() {
  const API_KEY='f12c2675';
  const API_ID='2f8bd99e445a899d241394a6ec8b3eda';
  useEffect(() => {
    let url=`search?q=chicken&app_id=${API_KEY}&app_key=${API_ID}`;
    fetch(url,{mode:"no-cors"})
    .then(response=>{
      return response.json();
    })
    .then(res=>{console.log("final response",res)
    })
    .catch((err)=>{
      console.log("error",err);
    })
  },[])

  return (
    <div className="App">
      <header className="App-header">
        <h3>Welcome App Component</h3>
       
      </header>
    </div>
  );
}

export default App;
